<template>
  <div class="main-container">
    <!-- Ê†πÊçÆ showHeader ÁöÑËÆ°ÁÆóÁªìÊûúÊòæÁ§∫ BHeader -->
    <header v-if="showHeader">
      <BHeader />
    </header>

    <!-- ÂΩì loginStatus ‰∏∫ true Êó∂ÊòæÁ§∫ -->
    <header v-if="loginStatus">
      <p>User is logged in</p>
    </header>

    <main class="main-box">
      <router-view></router-view>
    </main>
  </div>
</template>

<script setup>
import { ref, computed, watch } from "vue";
import { useRoute } from "vue-router";
import BHeader from './components/BHeader.vue';

const loginStatus = ref(false);
const route = useRoute();

// Ê†πÊçÆË∑ØÁî±ÂêçÁß∞Âä®ÊÄÅÂÜ≥ÂÆöÊòØÂê¶ÊòæÁ§∫ BHeader
const showHeader = computed(() => route.name !== 'CountBookAPI');

// ÁõëÂê¨Ë∑ØÁî±ÂêçÁß∞ÁöÑÂèòÂåñÂπ∂Âä®ÊÄÅÊõ¥Êñ∞ loginStatus
watch(
  () => route.name,
  (name) => {
    if (name) {
      const token = sessionStorage.getItem("token");
      loginStatus.value = !!token; // Â¶ÇÊûú token Â≠òÂú®ÂàôËÆæÁΩÆ loginStatus ‰∏∫ true
    }
  },
  { immediate: true }  // Á°Æ‰øùÂú®ÁªÑ‰ª∂ÊåÇËΩΩÊó∂Á´ãÂç≥ÊâßË°å
);
</script>

